import RenCalendar from '@/components/ren-calendar/ren-calendar.vue'
export default {
	components:{
		RenCalendar
	},
	data() {
		return {
			currentpage:"",
			score:"",
			template_id1:"",
			template_id2:"",
			template_id3:"",
			showpopup:false,
			curDate:'',
			markDays:[],
			//顶部导航栏
			height:"",
			background:{
				backgroundImage:"linear-gradient(90deg,  #54C3F1,#00ABEB)",
			},
			//顶部导航栏
			user_name:"",
			user_avatar:"",
			user_phone:"",
			user_address:"",
			number:'',
			pic: 'https://uviewui.com/common/logo.png',
			show: true,
			showtime: false,
			showsign: false,
			mode: 'date',
			timeval: "9:00——18:00",
			list: [
				[{
						value: '8:00',
						label: '8:00'
					},
					{
						value: '9:00',
						label: '9:00'
					}
				],
				[{
						value: '12:00',
						label: '12:00'
					},
					{
						value: '18:00',
						label: '18:00'
					}
				],

			],
		}
	},
	onLoad() {
		//检查状态
		this.checklogin();
		this.user_name = uni.getStorageSync("user_name");
		this.user_avatar = uni.getStorageSync("user_avatar");
		this.user_phone = uni.getStorageSync("phone");
		this.user_address = uni.getStorageSync("address");
		this.number = uni.getStorageSync("number");
		// console.log(this.user_address);
		this.SignList();
		this.getInfo();
		var pages = getCurrentPages();
		var page = pages[pages.length - 1];
		var currentpage = page.route;
		this.currentpage = currentpage;
		console.log(currentpage);
	},
	onReady() {
	    let today = this.$refs.ren.getToday().date;
	    this.curDate = today;
	    this.markDays.push(today);
	},
	methods: {
		//获取用户信息
		getInfo(){
			this.$myRequest({
				url:'user/getInfo',
				data:{},
				methods:"POST"
			}).then(res=>{
				console.log(res);
			// const data = JSON.parse(res.data);
				if(res.data.code == 200){
					console.log(res.data.msg);
					// this.data = res.data.data
					let arr  = res.data.data.work_time;
					let score  = res.data.data.score;
					this.timeval  =arr;
					this.score  =score;
				}else if(res.data.code == 300){
					console.log(res.data.msg);
			
				}else{
					console.log(res.data.msg)
				}
			})
		},
		//日历
		onDayClick(data){
		    this.curDate = data.date;
		},
		SignList(){
			this.$myRequest({
				url:'user/SignList',
				methods:"POST",
				data:{}
			}).then(res=>{
			// 	console.log(res);
			// const data = JSON.parse(res.data);
				if(res.data.code == 200){
					console.log(res.data.msg);
					console.log(res.data);
					console.log(res);
					this.markDays = res.data.data;
				}else if(res.data.code == 300){
					console.log(res.data.msg);
				}else{
					console.log(res.data.msg)
				}
			})	
		},
		Sign(){
			this.$myRequest({
				url:'user/Sign',
				methods:"POST",
				data:{
					time:this.curDate
				}
			}).then(res=>{
			// 	console.log(res);
			// const data = JSON.parse(res.data);
				if(res.data.code == 200){
					console.log(res.data.msg);
					console.log(res);
					uni.showToast({
						title:res.data.msg
					})
				}else if(res.data.code == 300){
					console.log(res.data.msg);
					uni.showToast({
						title:res.data.msg
					})
				}else{
					console.log(res.data.msg)
				}
			})	
		},
		//订阅消息
		async requestMsg(){
			
			await this.$myRequest({
				url:'user/getTemplateid',
				data:{},
			}).then(res=>{
				console.log(res);
			// const data = JSON.parse(res.data);
				if(res.data.code == 200){
					console.log(res.data.msg);
					console.log(res.data.data);
					this.template_id1 = res.data.data[0];
					this.template_id2 = res.data.data[1];
					this.template_id3 = res.data.data[2];
				}else if(res.data.code == 300){
					console.log(res.data.msg);

				}else{
					console.log(res.data.msg)
				}
			})
			// console.log(res.data);
			wx.requestSubscribeMessage({
			  tmplIds: [this.template_id1,this.template_id2,this.template_id3],
			  success (res) {
				  console.log("模板");
				  console.log(res);
				   if (res['c-QfMnWBUDkg2CIlBJDOYaGj6Bpn-p6g9HuKUi8LrXY'] === 'accept'){
				               wx.showToast({
				                 title: '订阅OK！',
				                 duration: 1000,
				    })
				}
			  }
			})
		},
		// 回调参数为包含多个元素的数组，每个元素分别反应每一列的选择情况
		confirm(e) {
			console.log(e);
			this.timeval = e[0].value + "——" + e[1].value;
		},
		//签到
		change(e) {
			console.log(e);
			uni.showToast({
				title:"签到成功",
				duration:1000
			})
		},
		//打开个人数据页面
		openmydata(){
			let userdata = {
				user_name : this.user_name,
				user_avatar: this.user_avatar,
				user_phone: this.user_phone,
				user_address:this.user_address
			}
			uni.navigateTo({
				url: "../mydata/mydata?userdata="+ encodeURIComponent(JSON.stringify(userdata))
			});
		},
		//打开个人信息页面
		openinfo(){
			let userinfo = {
				user_name : this.user_name,
				user_avatar: this.user_avatar,
				user_phone: this.user_phone,
				user_address:this.user_address
			}
			uni.navigateTo({
				url:"../myinfo/myinfo?userinfo="+ encodeURIComponent(JSON.stringify(userinfo))
			})
		},
		// 打开设置权限
		openset(){
			wx.openSetting({
			  success (res) {
			    console.log(res.authSetting)
			  }
			})
		},
		//开发中
		showcoder(){
			uni.showToast({
				title:"开发中"
			})
		},
		//拨打客服电话
		go(){
		 	uni.makePhoneCall({
		 	
		 	// 手机号
		    phoneNumber: '400-0015-021', 
		
			// 成功回调
			success: (res) => {
				console.log('调用成功!')	
			},
		
			// 失败回调
			fail: (res) => {
				console.log('调用失败!')
			}
			
		  });
		}
	},
	//自定义分享页面
	onShareAppMessage(e){
		return {
			title: this.$overShare.title,
			path: this.currentpage,
			imageUrl:this.$overShare.imageUrl,
			
		}
	}
}
